.BasicAssistant {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.BasicAssistant-image {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  position: relative;
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 50%,
    rgba(0, 0, 0, 0) 100%
  );
  mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 50%,
    rgba(0, 0, 0, 0) 100%
  );
}

.BasicAssistant-mic-button {
  position: absolute;
  bottom: 15px;
  border: none;
  padding: 16px;
  border-radius: 50%;
  cursor: pointer;
}


