.BasicAssistant-mic-icon {
  width: 100px;
}

.BasicAssistant-mic-button:hover {
  filter: brightness(0.95);
}

.BasicAssistant-mic-button.active {
  filter: brightness(0.9);
}

/* Tablet & Mobile Adjustments */
@media (max-width: 1024px) {
  .BasicAssistant {
    height: 100%;
  }

  .BasicAssistant-image {
    max-width: 50%;
  }

  .BasicAssistant-mic-icon {
    width: 70px;
  }

  .BasicAssistant-mic-button {
    padding: 12px;
    bottom: 10px;
  }
}
