.container {
  display: flex;
  flex-direction: row;
  /* gap: 16px; */
  gap: 12vw;
  align-items: center;
  justify-content: center;
  padding: 16px;
  box-sizing: border-box;
  height: 100vh;
  width: 90vw;
  margin: auto;
}

/* .input-panel {
  width: 30%;
  min-height: 570px;
  border-radius: 5px;
  flex-shrink: 0;
  flex-grow: 0;
} */

.assistant {
  max-width: 60%;
  height: 85vh;
  /* max-height: 100vh; */
  flex-shrink: 0;
  flex-grow: 1;
}

.chat-panel {
  width: 30%;
  height: 570px;
  min-height: 0px;
  border-radius: 4px;
  flex-shrink: 0;
  flex-grow: 0.5;
}
/* Mobile & Tablet Responsive */
@media (max-width: 1024px) {
  .container {
    flex-direction: column;
    height: 100vh;
    padding: 8px;
  }

  /* Hide Chat Panel on Android & Tablet */
  .chat-panel {
    display: none;
  }

  /* Input Panel - Positioned at the top, grows dynamically */
  .input-panel {
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 500px;
    z-index: 10;
    height: 0vh;
    transition: height 0.3s ease-in-out;
  }

  .input-panel.active {
    height: calc(100vh - 100px);
  }

  .input-panel.fullscreen {
    height: 100vh;
  }

  /* Assistant Panel - Always occupies the bottom half */
  .assistant {
    max-width: 100%;
    height: 50vh;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}


/* a background line at 10degree with white on one side and navy blue on other side */
.ea-line-bg{
  background: linear-gradient(
    80deg,
    #ffffff 53%,
    #05387b 53%,
    #083f88 75%,
    #0e50a5 100%
  );
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}