/* Root Variables */
:root {
  --theme-bg-primary: #17265B;
  --theme-bg-secondary: #17265B14;

  --theme-primary: #17265B;
  --theme-white: #ffffff;
  --theme-black: #000000;
}

/* Background Colors */
.bg-primary {
  background-color: var(--theme-bg-primary);
}

.bg-secondary {
  background-color: var(--theme-bg-secondary);
}

/* Text Colors */
.text-primary {
  color: var(--theme-primary);
}

.text-black {
  color: var(--theme-black);
}

/* Borders */
.border-primary {
  border: 1px solid var(--theme-primary);
}

.border-black {
  border: 1px solid var(--theme-black);
}

/* Buttons */
.btn-primary {
  background-color: var(--theme-primary);
  color: var(--theme-white);
  padding: 10px 16px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.btn-primary:hover {
  background-color: var(--theme-bg-primary);
  transform: scale(1.05);
}

.btn-primary:active {
  transform: scale(0.95);
  opacity: 0.8;
}

/* THEME CLASSES */
.theme-primary {
  background-color: var(--theme-primary);
  color: var(--theme-white);
}

.theme-secondary {
  background-color: var(--theme-bg-secondary);
  color: var(--theme-black);
}
