/* Parent container */
.ChatPanel-cards {
  position: relative;
  width: 100%; /* Adjust max width if needed */
  margin: 0 auto;
}

/* Slider container */
.ChatPanel-cards-slider {
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
}

/* Individual card */
.ChatPanel-cards-slider-item {
  width: 100%;
  position: absolute;
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  z-index: 0; /* Ensure non-active elements are behind */
  pointer-events: none; /* Prevent clicking on hidden cards */
}

.ChatPanel-cards-slider-item-active {
  opacity: 1;
  transform: scale(1);
  position: relative;
  z-index: 10; /* Ensure active element is above */
  pointer-events: auto; /* Allow clicking */
}

/* Image container */
.ChatPanel-cards-image-container {
  position: relative;
  width: 100%;
  height: auto;
  border-radius: 10px;
  overflow: hidden;
}

/* Card image */
.ChatPanel-cards-image {
  width: 100%;
  height: auto;
  display: block;
  border-radius: 10px;
}

/* Card label */
.ChatPanel-cards-label {
  position: absolute;
  bottom: 10px;
  left: 10px;
  background: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 5px 10px;
  font-size: 14px;
  border-radius: 5px;
}

/* Navigation buttons on image */
.ChatPanel-cards-slider-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: #fff;
  color: #000;
  border: none;
  padding: 18px 20px;
  cursor: pointer;
  border-radius: 50%;
  z-index: 20; /* Ensure buttons are above image */
  display: flex;
  align-items: center;
  justify-content: center;
}

.ChatPanel-cards-slider-btn-left {
  left: 10px;
}

.ChatPanel-cards-slider-btn-right {
  right: 10px;
}

/* Ensure both buttons are perfectly aligned */
.ChatPanel-cards-slider-btn-left,
.ChatPanel-cards-slider-btn-right {
  top: 50%;
  transform: translateY(-50%);
}

/* Options section - Now below the image */
.ChatPanel-cards-options {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
  align-items: center;
  z-index: 15; /* Ensure options are above everything */
}

/* Options buttons */
.ChatPanel-cards-option {
  padding: 10px 15px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 14px;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  width: 90%;
  max-width: 300px;
}

.ChatPanel-cards-option:hover {
  background-color: #d1d5db;
}
