.BasicInput-buttons {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.BasicInput-label {
  font-size: 14px;
  color: #eee;
}

.BasicInput-group {
  display: flex;
}

.BasicInput-input {
  flex: 1;
  padding: 12px;
  font-size: 18px;
  border: 1px solid #ccc;
  /* border-radius: 8px 0 0 8px; */
  outline: none;
}

.BasicInput-send {
  padding: 10px 16px;
  background: #007bff;
  color: #fff;
  border: none;
  /* border-radius: 0 8px 8px 0; */
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

.BasicInput-send:hover {
  background: #0056b3;
}
