/* Config */
:root {
  --trigger-margin: 20px;
  --trigger-image-size: 100px;
}

/* Tablet Adjustments */
@media (max-width: 1024px) {
  :root {
    --trigger-margin: 18px;
    --trigger-image-size: 90px;
  }
}

/* Mobile Adjustments */
@media (max-width: 768px) {
  :root {
    --trigger-margin: 15px;
    --trigger-image-size: 80px;
  }
}

/* Small Mobile Adjustments */
@media (max-width: 480px) {
  :root {
    --trigger-margin: 10px;
    --trigger-image-size: 60px;
  }
}

/* Trigger */
.embed-trigger {
  position: fixed;
  right: var(--trigger-margin);
  bottom: var(--trigger-margin);

  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;

  transition: transform 0.15s ease-in-out; /* Further reduced jumpiness */
  z-index: 1000;
}

.embed-trigger:active {
  transform: scale(0.92); /* Smooth click effect */
}

.embed-trigger-image {
  width: auto;
  height: var(--trigger-image-size);
  position: relative;
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 50%,
    rgba(0, 0, 0, 0) 100%
  );
  mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 50%,
    rgba(0, 0, 0, 0) 100%
  );
}

.close-icon {
  color: black;
  font-size: calc(var(--trigger-image-size) * 0.8); /* Scales slightly smaller */
}
