/* Items : Message */

.ChatPanel-message {
  background-color: #111;
  color: #fff;
  padding: 15px;
  border-radius: 8px;
  font-size: 16px;
  width: 80%;
  align-self: flex-start;
  white-space: pre-line;
}
.ChatPanel-message:not(.bot) {
  align-self: flex-end;
  background: #fff;
  color: #333;
}

/* Items : Buttons */

.ChatPanel-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Items : Buttons : Options */

.ChatPanel-option {
  padding: 12px;
  background-color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}
.ChatPanel-option:hover {
  background-color: #d1d5db;
}
