
/* Config */
:root{
  --trigger-margin: 25px;
  --trigger-image-size: 120px;
}

/*******
  Parent
********/


/*********
  Trigger
**********/


/* Trigger */
.Slideup-trigger {
  position: fixed;
  right: var(--trigger-margin);
  bottom: var(--trigger-margin);

  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;

  transition: transform 0.3s ease-in-out;
  z-index: 1000;
}

.Slideup-trigger:hover {
  transform: scale(1.1) translate(-5px, -5px);
}


/*******
  Panel
********/

.Slideup-panel {
  width: 100vw;
  height: 100vw;

  position: fixed;
  top: 0;
  left: -100vw;
  bottom: 0;
  transform: none!important;
  
  z-index: 999;
}
.Slideup.active .Slideup-panel{
  left: 0;
  right: 0;
}

/* Float > Modal */
.Slideup-modal{
  height: 100vh;
  overflow: auto;
}